import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

/**     */
//  Slice          "/"   .
export const getBookList = createAsyncThunk("BOOK/GET_LIST", async (payload, { rejectWithValue }) => {
    let result = null;

    //       
    if(payload){
      try {
        const apiUrl = 'https://dapi.kakao.com//v3/search/book';
        result = await axios.get(apiUrl, {
          params: {
              query: payload, 
          },
          headers:{
              Authorization:'KakaoAK a4711b1716f82c5243d4ad07178e749b'
          }
        });
  
      } catch (err) {
        result = rejectWithValue(err.response);
      }
    }
    
    return result;
  }
);

/** Slice  (Action + Reducer ) */
const bookSlice = createSlice({
    name: "book",
    initialState: {
      /**    */
      rt: null,       // HTTP  (200,404,500 )
      rtmsg: null,    // 
      item: [],       // Ajax    
      loading: false, //  
    },
    //  action   action (Ajax  )
    reducers: {},
    //  action   action
    extraReducers: {
      [getBookList.pending]: (state, { payload }) => {
        return { ...state, loading: true };
      },
      [getBookList.fulfilled]: (state, { payload }) => {
          return {
              ...state,
              rt: payload.status,
              rtmsg: payload.statusText,
              item: payload.data,
              loading: false,
          };
      },
      /** Ajax   */
      [getBookList.rejected]: (state, { payload }) => {
        return {
          ...state,
          rt: payload?.status ? payload.status :'500',
          rtmsg: payload?.statusText ? payload.statusText : 'Server Error',
          item: payload?.data,
          loading: false,
        };
      },
    },
  });
  
  //   
  export default bookSlice.reducer;
  