import React from 'react';
import styled from 'styled-components';
import { NavLink , useNavigate } from 'react-router-dom';

const MenuLink = styled(NavLink)`
    font-size: 20px;
    cursor: pointer;
    text-decoration: none;
    padding-bottom: 2px;
    color: #222;
    line-height: 40px;

    &:hover{
        color: #22b8cf;
    }

    &:after{
        content: '|';
        display: inline-block;
        padding: 0 7px;
        color: #ccc;
    }

    &:last-child{
        &:after{
            color: #fff;
        }
    }

    &.active{
        text-decoration: underline;
        color: #22b8cf;
        &:after{
            border-bottom: 4px solid #fff !important;
        }
    }
`;

const Top = () => {

    // 1-1) HTML      
    const inputQuery = React.useRef();

    // 2-3)    ->   
    const [query, setQuery] = React.useState("");

    // 3-1)     
    const navigate = useNavigate();

    // 4-1)    
    const handleSubmit = (e) => {
        e.preventDefault();

        //1-3) input   
        const value = inputQuery.current.value;

        if(!value){
            inputQuery.current.focus();
            alert(' .');
            return;
        }

        // 2-2)    .
        setQuery(value);

        // 3-2)     
        navigate(`/web?query=${encodeURIComponent(value)}`);
    };


    return (
        <div>
            <h1> </h1>
            <hr/>
            {/** 4-2) submit       */}
            <form onSubmit={handleSubmit}>
                {/** 1-2)        */}
                <input type="search" name="query" ref={inputQuery}/>
                <button type="submit"></button>
            </form>
            <hr/>

            {/** 2-3) query    . */}
            {query && (
                <nav>
                    <MenuLink to={`/web?query=${encodeURIComponent(query)}`}></MenuLink>
                    <MenuLink to={`/image?query=${encodeURIComponent(query)}`}></MenuLink>
                    <MenuLink to={`/blog?query=${encodeURIComponent(query)}`}></MenuLink>
                    <MenuLink to={`/cafe?query=${encodeURIComponent(query)}`}></MenuLink>
                    <MenuLink to={`/book?query=${encodeURIComponent(query)}`}></MenuLink>
                </nav>
            )}
            
        </div>
    );
};

export default Top;